.body {
  justify-content: center;
  align-items: center;
  width: 100vmin;
  height: 100vmin;
  margin: 0 auto;
  display: flex;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__circle {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

.stopwatch__minutes-hand {
  z-index: 0;
  transform-origin: bottom;
  box-sizing: border-box;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite move;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
}

.stopwatch__minutes-hand--speed-up {
  animation-duration: 600s;
}

.stopwatch__seconds-hand {
  z-index: 1;
  transform-origin: bottom;
  box-sizing: border-box;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - .75vmin);
}

.stopwatch__seconds-hand--speed-up {
  animation-duration: 10s;
}
